<script>
import { createNamespacedHelpers, mapGetters } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("bookModule");
export default {
  data() {
    return {
      value: "",
      banner: {
        male: [
          require("../assets/images/1.jpg"),
          require("../assets/images/2.jpg"),
          require("../assets/images/3.jpg"),
          require("../assets/images/4.jpg"),
          require("../assets/images/5.jpg"),
          require("../assets/images/6.jpg"),
        ],
        female: [
          require("../assets/images/7.jpg"),
          require("../assets/images/8.jpg"),
          require("../assets/images/9.jpg"),
          require("../assets/images/10.jpg"),
          require("../assets/images/11.jpg"),
          require("../assets/images/12.jpg"),
          require("../assets/images/13.jpg"),
        ],
      },
      popup: [
        {
          icon: "apps-o",
          text: "分类",
          route: "Class",
        },
        {
          icon: "bar-chart-o",
          text: "排行榜",
          route: "Ranking",
        },
        {
          icon: "orders-o",
          text: "书架",
          route: "Bookshelf",
        },
      ],
      isSex: 0,
      maleData: {},
      femaleData: {},
      m: [],
      f: [],
    };
  },
  created() {
    this.getHotBookClass();
    this.isSex = this.homeSexCache;
    console.log(this.isSex);
  },
  computed: {
    ...mapState(["maleDataCache", "femaleDataCache", "homeSexCache"]),
  },
  methods: {
    ...mapMutations(["changeHome"]),
    getHotBookClass() {
      let hotBookClassCache = [];
      if (this.maleDataCache.length > 0 && this.femaleDataCache.length > 0) {
        this.maleData = this.maleDataCache;
        this.femaleData = this.femaleDataCache;
        console.log("缓存了");
        return;
      }
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0,
      });
      this.axios({
        method: "GET",
        url: "https://novel.kele8.cn/rank-category",
      })
        .then((result) => {
          this.$toast.clear();

          if (Object.keys(result.data).length > 0) {
            let male = [];
            male[0] = result.data.male[0]._id;
            male[1] = result.data.male[3]._id;
            male[2] = result.data.male[5]._id;
            let female = [];
            female[0] = result.data.female[0]._id;
            female[1] = result.data.female[3]._id;
            female[2] = result.data.female[5]._id;
            for (let i = 0; i < 3; i++) {
              this.getHotBookData("male", male[i], i);
              this.getHotBookData("female", female[i], i);
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    getHotBookData(sex, id, name) {
      let data = [];
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0,
      });
      this.axios({
        method: "GET",
        url: "https://novel.kele8.cn/rank/" + id,
      })
        .then((result) => {
          this.$toast.clear();
          if (Object.keys(result.data).length > 0) {
            for (let i = 0; i < result.data.ranking.books.length; i++) {
              result.data.ranking.books[i].cover = this.imgKey(
                result.data.ranking.books[i].cover
              );
            }
            if (Object.keys(result.data.ranking.books).length > 3) {
              data = result.data.ranking.books.slice(0, 4);
            } else {
              data = result.data.ranking.books;
            }
            if (sex == "male") {
              if (name == 0) {
                this.m[name] = data;
              } else if (name == 1) {
                this.m[name] = data;
              } else {
                this.m[name] = data;
              }
              setTimeout(() => {
                this.maleData = this.m;
                //缓存主页数据
                this.changeHome({
                  key: "maleDataCache",
                  value: this.m,
                });
              }, 200);
            } else {
              if (name == 0) {
                this.f[name] = data;
              } else if (name == 1) {
                this.f[name] = data;
              } else {
                this.f[name] = data;
              }
              setTimeout(() => {
                this.femaleData = this.f;
                //缓存主页数据
                this.changeHome({
                  key: "femaleDataCache",
                  value: this.f,
                });
              }, 200);
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    popupClick(item) {
      let isSex = this.isSex;
      this.$router.push({ name: item.route, query: { isSex: isSex } });
    },
    bookshelf() {
      this.$router.push({ name: "Bookshelf" });
    },
    login() {
      this.$router.push({ name: "Login" });
    },
    change(index) {
      this.isSex = index;
      this.changeHome({
        key: "homeSexCache",
        value: index,
      });
    },
    imgKey(value) {
      return unescape(value).slice(7);
    },
    focus() {
      this.$router.push({ name: "Search" });
    },
    bannerClick(index, isSex) {
      let arrId = [];
      if (Boolean(isSex)) {
        arrId = [
          "5bf7b36d7cdc9d655de91fa0",
          "5cbdabdf82ef180de905082d",
          "5c75016ec974f954eea4906a",
          "5b879366f4f6687e8fdd67c6",
          "53e020642ad91059430caddf",
          "5c2c8862ae59061b689dd55d",
          "5eec0d8c13d8458529dc4f33",
        ];
      } else {
        arrId = [
          "5ed0c1a63f63967520f3b7df",
          "5e23ba2082bd0f5b7a50f88d",
          "5d5cf87f67621e4f2d38640e",
          "5ea0f99468df50e52f18fb35",
          "566d90bb6372c7a150c40f54",
          "59a5aff764e6a8d8628d5191",
        ];
      }
      let urlId = arrId[index];
      this.$router.push({
        name: "Detail",
        query: { id: urlId },
      });
    },
    hotClick(item) {
      let _id = item._id;
      this.$router.push({
        name: "Detail",
        query: { id: _id },
      });
    },
  },
};
</script>
